.form__item {
  border-bottom: $border-line;

  & input,
  & textarea,
  & input::placeholder,
  & textarea::placeholder {
    font-family: $font-family-title;
    text-transform: uppercase;
    font-size: 16px;
  }

  & textarea {
    border: $border-input;
  }
}

.form__owner,
.form__comment,
.form__additional {
  border-bottom: none;
  position: relative;
}

.form__info,
.form__radio,
.form__checkbox {
  padding: 30px 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  list-style: none;
}

.form__radio {
  min-height: 157px;
  padding: 20px 0 23px;
}

.form__title {
  display: inline-flex;
  background-color: #ffffff;
  font-size: 24px;
  padding-top: 25px;
  line-height: 1;
  padding-right: 22px;

  .form__owner & {
    max-width: 203px;
  }

  &::before,
  &::after {
    position: absolute;
    top: 38px;
    content: "";
    display: block;
    background-color: $color-green;
    width: 100vw;
    height: 3px;
    z-index: -2;
  }

  &::before {
    right: 0;
    display: none;
  }

  &::after {
    left: 0;
  }
}

.form__field,
.form__tick,
.form__box {
  display: flex;
  align-items: center;
}

.form__field {
  position: relative;
  justify-content: space-between;
  margin-top: 10px;
}

.form__tick,
.form__box {
  justify-content: left;
}

.form__tick input,
.form__box input {
  width: 30px;
  height: 30px;
  margin-right: 15px;
}

//Customize the radio form
.form__tick-item,
.form__checkbox-item {
  position: relative;
  padding-left: 46px;
  display: flex;
  align-items: center;
  cursor: pointer;

  min-height: 30px;
}

.form__tick-item input,
.form__checkbox-item input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

.form__tick-mark,
.form__checkbox-mark {
  position: absolute;
  top: 0;
  left: 0;
  height: 30px;
  width: 30px;
  border: $border-input;
}

.form__tick-mark {
  border-radius: 50%;
}

.form__tick-item:hover input ~ .form__tick-mark,
.form__tick-item:focus input ~ .form__tick-mark,
.form__checkbox-item:hover input ~ .form__checkbox-mark,
.form__checkbox-item:focus input ~ .form__checkbox-mark {
  border: $border-input-hvr;
}

.form__tick-item:active input ~ .form__tick-mark,
.form__checkbox-item:active input ~ .form__checkbox-mark {
  border: $border-input-hvr;
  opacity: 0.3;
}

.form__tick-mark::after,
.form__checkbox-mark::after {
  content: "";
  position: absolute;
  display: none;
}

.form__tick-item input:checked ~ .form__tick-mark::after,
.form__checkbox-item input:checked ~ .form__checkbox-mark::after {
  display: block;
}

.form__tick-item .form__tick-mark::after {
  top: 8px;
  left: 8px;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: $color-green;
}

.form__checkbox-item .form__checkbox-mark::after {
  content: "";
  background-image: url("../img/icon-tick.svg");
  background-repeat: no-repeat;
  width: 22px;
  height: 17px;
  top: 5px;
  left: 4px;
}

.form__item label {
  margin-right: 15px;
  text-transform: uppercase;
}

.form__info input {
  flex-grow: 1;
  min-height: 45px;
  padding-left: 14px;
  border: $border-input;
  outline: none;

  &:hover,
  &:focus {
    border: $border-input-form-hvr;
  }

  &:active,
  &:focus-within {
    border: $border-input-form-active;
  }

  &.error {
    border: $border-input-form-error;
    outline: none;
  }
}

.form__radio input {
  width: 30px;
  min-height: 30px;
}

.form__pet-info,
.form__additional-info {
  min-height: 225px;
  padding: 35px 0;
}

.form__pet-info {
  padding: 28px 0 24px;
}

.form__owner-info {
  min-height: 142px;
  padding: 25px 0 5px;
}

.form__owner-info label {
  margin-right: 11px;
}

.form__owner-items {
  position: relative;
}

.form__owner-item {
  position: absolute;
  top: 15px;
  right: 15px;
  fill: $color-dark;

  &--email {
    width: 18px;
    height: 14px;
  }

  &--phone {
    width: 21px;
    height: 21px;
  }
}

.form__comment-title {
  padding-bottom: 33px;
}

.form__comment textarea {
  padding: 9px 10px 0 14px;
  min-height: 150px;
  width: 100%;
}

@media (min-width: $tablet-width) {
  .form__item {
    border-bottom: none;

    & input,
    & textarea,
    & input::placeholder,
    & textarea::placeholder {
      font-size: 20px;
    }
  }

  .form__item label {
    margin-right: 19px;
  }

  .form__info input {
    min-height: 52px;
    padding-left: 18px;
  }

  .form__field {
    margin-top: 20px;
  }

  .form__radio {
    padding: 27px 28px 0;
    margin-bottom: 53px;
    border: $border-input;
  }

  .form__radio input,
  .form__box input {
    width: 40px;
    height: 40px;
  }

  .form__tick {
    margin-bottom: 24px;
  }

  .form__tick input {
    margin-right: 19px;
  }

  .form__title {
    padding-right: 40px;
    font-size: 36px;

    .form__owner & {
      max-width: 100%;
    }

    &::before,
    &::after {
      top: 49px;
    }

    &::before {
      display: block;
      width: 30px;
      left: -70px;
    }
  }

  .form__pet-info {
    padding: 45px 0 72px;
  }

  .form__owner-info {
    padding: 55px 0 39px;
  }

  .form__pet-info,
  .form__additional-info {
    min-height: 217px;
  }

  .form__comment {
    margin-bottom: 35px;
  }

  .form__comment textarea {
    min-height: 174px;
    padding-left: 17px;
    margin-top: 44px;
  }

  .form__checkbox {
    padding: 40px 0;
    flex-direction: row;
    flex-wrap: wrap;
  }

  .form__box {
    min-width: 45%;
  }

  .form__checkbox-item,
  .form__tick-item {
    padding-left: 58px;
  }

  .form__checkbox-mark,
  .form__tick-mark {
    width: 40px;
    height: 40px;
    top: -5px;
  }

  .form__tick-item .form__tick-mark::after {
    width: 18px;
    height: 18px;
    top: 10px;
    left: 10px;
  }

  .form__checkbox-item .form__checkbox-mark::after {
    top: 10px;
    left: 7px;
  }
}

@media (min-width: $desktop-width) {
  .form__pet {
    max-width: $desktop-width;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;

    & .form__item {
      flex-basis: 46.7%;
    }
  }

  .form__radio {
    min-height: 196px;
    margin: 0;
    padding: 25px 28px 16px;
  }

  .form__tick {
    margin-bottom: 17px;
  }

  .form__title {
    padding: 0 40px 0 0;

    &::before,
    &::after {
      top: 20px;
    }

    &::before {
      width: 160px;
      left: -200px;
    }
  }

  .form__owner-info {
    flex-direction: row;
    min-height: 52px;
    padding: 49px 0 70px;

    & .form__field {
      flex-basis: 46.7%;
    }
  }

  .form__owner-item {
    top: 20px;
    right: 19px;
  }

  .form__comment {
    margin-bottom: 63px;
  }

  .form__comment textarea {
    margin-top: 70px;
  }

  .form__additional-info {
    min-height: 143px;
  }

  .form__box {
    min-width: 20%;
  }
}
